<template>
    <div class="shop-box">
        
        <header>
            <div class="header-one"><img :src="'//elm.cangdu.org/img/'+arr.image_path" alt=""></div>
            <ul class="shop-header-ul">
                <li>
                    <section class="header-section">
                        <div class="div-img"><img :src="'//elm.cangdu.org/img/'+arr.image_path" alt=""></div>
                        <div>
                            <p>{{arr.name}}</p>
                            <p>商家配送 / {{arr.order_lead_time}}送达 / 配送费约￥{{arr.float_delivery_fee}}</p>
                            <p>公告：{{arr.promotion_info}}</p>
                        </div>    
                    </section>
                    <figcaption>
                        <span>满</span>{{array.description}}
                    </figcaption>
                </li>
            </ul>
        </header>
        <!-- 导航 -->
         <nav>
            <p @click="Faln()">商品</p>
            <p @click="Fal()">评价</p>
         </nav>
        <!-- 主体 -->
        <article class="shop-con">
            
                <!-- 商品 -->
               <section v-if="faln" class="shop-commodity">
                    <!-- 侧边导航 -->
                    <ul class="shop-commodity-ul">
                        <li v-for="(i,k) in list" :key="k" :class="{active:k==iN}"  @click="chengeColor(k)"><a :href="'#skip'+k">{{i.name}}</a></li>
                    </ul>


                    <!-- 右边内容 -->
                    <div class="shop-commodity-div">
                        <article v-for="(item,k) in list" :key="k">
                            <div class="shop-hotSale" :id="'skip'+k">
                                <span>{{item.name}}</span><span>{{item.description}}</span>
                            </div>

                            <ul v-for="(i,index) in item.foods" :key="index" class="shop-hotSale-ul">
                               <li>
                                   <img :src="'//elm.cangdu.org/img/'+i.image_path" alt="">
                                   <ol>
                                       <li>{{i.name}}</li>
                                       <li>{{i.description}}</li>
                                       <li class="shop-hard">
                                           <span>月售{{i.month_sales}}</span>  好评率{{i.satisfy_rate}}%
                                           <!-- <p>{{i.activity.name}}</p> -->
                                        </li>
                                       <li><span>{{i.item_id}}</span></li>
                                       <li v-for="(m,j) in i.specfoods" :key="j" class="shop-lastLi">
                                          
                                            <p>  <span class="shop-hotSale-span">￥</span><span>{{m.price}}</span>  <span class="shop-hotSale-span">起</span> </p>
                                            <p>
                                                <button v-if="i.is_featured>0" @click="Minus(i)" class="faB">-</button> 
                                                
                                                 <span v-if="i.is_featured>0">{{i.is_featured}}</span>

                                                <button @click="AllPowerful($event, i,m.price)">+</button></p>

                                        </li>
                                   </ol>
                               </li>
                            </ul>
                        </article>
                        
                    </div>
                </section>
                
               <!-- 评价 -->
               <section v-if="!faln" class="evaluate">
                  <img src="../../assets/evaluate.png" alt="">
               </section>
                
        </article>

        <!-- 购物车面板 -->
        <section class="shop-show">
            <van-popup v-model="show" position="bottom" tag="div" class="shop-didi" :style="{width:'100%',paddingBottom:'0.77rem'}">
                <div class="shop-show-one">
                    <p>购物车</p>
                    <p @click="Del()"><van-icon name="delete" color=" rgb(187, 187, 187)" tag="span"/>  <span>清空</span></p>
                </div>
                <ul v-for="(v,s) in arrayAllTow" :key="s" class="shop-show-ul">
                    <li>{{v.name}}</li>
                    <li>￥<span>{{price}}</span></li>
                    <li>
                        <button @click='faA(v)' class="faA">-</button>
                        <span>{{v.is_featured}}</span>
                        <button @click="v.is_featured++">+</button>
                    </li>
                </ul>
            </van-popup>
        </section>      
        <!-- 脚 -->
        
        
        <footer class="shop-fot">
            
            <div @click="showPopup()" ref="ColorBlue" class="iconfont icon-gouwuche1-copy sc"></div>
            <div>
                <p class="shop-fot-oneP"><span>￥</span><span>{{this.$store.state.sum}}</span></p>
                <p class="shop-fot-towP"><span>配送费</span><span>￥5</span></p>
            </div>
            <div ref="ColorGreen" v-if="!Gap">还差￥5起送</div>
            <div ref="ColorGreen" v-if="Gap">去结算</div>
            <div class="ball" v-if="ballShow">{{this.$store.state.subAll}}</div>
        </footer>
    </div>
</template>

<script>
import Parabola from '@/page/theball/theBall.js'
import '@/page/theball/theBall.css'

export default {
    name:'shopDetails',
    comments:{},
    data() {
        return {
            faln:true,          //商品评价切换
            list:[],            //商品侧边导航渲染数组
            arr:[],             //头部
            array:[],           //头部

            iN:-1,              //侧边导航添加背景

            arrayAll:[],        //底部购物车存进vuex
            arrayAllTow:[],     //底部购物车从vuex取出
            price:'',           //单价

            show:false,         //购物车弹出层

            ballShow:false,      //小圆球
            Gap:false,            //差价
        }
    },
    methods: {
        /*  1商品/评价切换  */
        Faln(){
            this.faln=true;
        },
        Fal(){
            this.faln=false;
        },
        /*  2侧边导航加样式  */
        chengeColor(index){
            //console.log(index)
            this.iN=index
        },
         /*  3+号事件   万能all-powerful  */
         AllPowerful(e,i,price){
            
            /*  1.>抛物线函数调用  */
            Parabola(e)
            //减号和数量显示
            i.is_featured++;

            /*  2.>给i去重  */
            var index=this.arrayAll.findIndex((s)=>{
                return s.name==i.name
            })
            //console.log(index)
            if(index==-1){
                this.arrayAll.push(i)
                //把i存进vuex和本地中
                this.$store.commit('towshop',this.arrayAll)
                this.$store.commit('price',price)
            }else{
                this.arrayAll[index].is_featured++;
            }

            /*  3.>把vuex和本地存储取出来   */
            this.arrayAllTow=this.$store.state.towshop
            this.price=this.$store.state.price
                // console.log(this.arrayAllTow)
           
            
            /*  4.>总数量    总价钱存进vuex   */
            var sum=0
            var sub=0   
            for(var i=0;i< this.list.length;i++){
                // console.log(this.list[i].foods)
               for(var j=0;j<this.list[i].foods.length;j++){
                    sum+=this.list[i].foods[j].is_featured*this.price ;
                    sub+=this.list[i].foods[j].is_featured;
               }
            }
            //存进vuex
            this.$store.commit('sum',sum)
            this.$store.commit('subAll',sub)
            // console.log(this.$store.state.subAll)
            // console.log(this.$store.state.sum)
            
            /*  5.>样式变化 */
            if(this.$store.state.sum>5){
                this.Gap=true;
                //背景颜色
                this.$refs.ColorBlue.style.backgroundColor='#3190e8'
                this.$refs.ColorGreen.style.backgroundColor=' #56d176'
                this.ballShow=true
            }else{
                this.ballShow=false
            }
        },
        //减号事件
        Minus(i){
            i.is_featured--;
            this.$store.state.subAll--;
            /*  帮顶购物车减号，一点击事件控制两数量 this.$el.querySelector('.faA').click()   */
            if(this.$store.state.subAll<1){
                 this.$refs.ColorBlue.style.backgroundColor='#3d3d3f'
                 this.$refs.ColorGreen.style.backgroundColor='#535356'
                 this.$store.state.sum=0;
                 this.ballShow=false
                  this.Gap=false;
            }else{
                this.ballShow=true
                //减过后总价和总数发生了变化所以再存一次
                 var sum=0
                var sub=0   
                for(var i=0;i< this.list.length;i++){
                    // console.log(this.list[i].foods)
                for(var j=0;j<this.list[i].foods.length;j++){
                        sum+=this.list[i].foods[j].is_featured*this.price ;
                        sub+=this.list[i].foods[j].is_featured;
                }
                }
                //存进vuex
                this.$store.commit('sum',sum)
                this.$store.commit('subAll',sub)
                //console.log(this.$store.state.sum,this.$store.state.subAll)
            }

            // if(this.$store.state.sum<5){
            //     this.Gap=false;
            //      /*  3.背景颜色  */
            //       this.$refs.ColorBlue.style.backgroundColor='#3d3d3f'
            //      this.$refs.ColorGreen.style.backgroundColor='#535356'
            //     this.$store.state.sum=0;
            //      this.ballShow=false
            // }else{
            //     this.ballShow=false
            // }
        },
        //购物车减号
        faA(v){
            // console.log(v)
            this.$store.state.subAll--;//总数量--

            for(var i=0;i<this.arrayAllTow.length;i++){
                if(this.arrayAllTow[i].item_id==v.item_id){
                    if(this.arrayAllTow[i].is_featured<2){
                        this.arrayAllTow.splice(i,1)
                    }else{
                        this.arrayAllTow[i].is_featured--;
                    }
                }
            }

            if(this.$store.state.subAll<1){
                this.$refs.ColorBlue.style.backgroundColor='#3d3d3f'
                this.$refs.ColorGreen.style.backgroundColor='#535356'
                this.$store.state.sum=0;
            }
            if(this.$store.state.subAll>=1){
                this.ballShow=true
            }else{
                this.ballShow=false
            }
        },
        //清空
        Del(){
            this.arrayAllTow.splice(0,this.arrayAllTow.length);
            for(var i=0;i< this.list.length;i++){
                // console.log(this.list[i].foods)
               for(var j=0;j<this.list[i].foods.length;j++){
                   //console.log(this.list[i].foods[j].is_featured)
                   this.list[i].foods[j].is_featured=0
                   this.$refs.ColorBlue.style.backgroundColor='#3d3d3f'
                    this.$refs.ColorGreen.style.backgroundColor='#535356'
                    this.$store.state.sum=0;
                    this.$store.state.subAll=0;
               }
            }
        },
        //弹出层
        showPopup(v) {
            if(this.arrayAllTow){
                this.show = !this.show;   //显示隐藏
            }
        },
        

    },
    mounted() {
        //所有数据
        this.axios.get("../../../static/json/shop.json").then((res)=>{
            // console.log(res)
            this.list=res.data
        })
        /*头部换皮肤：
          1.先把所有vuex中存好的数据获取过来    
          2.循环这个数组中的每条数据，
          拿每条数据的id(this.$route.params.id)==上个页面中传过来的单个数据id(Img[i].id)
          3.就可以让他们同步传过来一条数据中    */
        var Img=this.$store.state.Image
        for(let i=0;i<Img.length;i++){
            // console.log(Img[i])

            if(this.$route.params.id==Img[i].id){
                    this.arr=Img[i];
                    this.array=Img[i].activities[0]
            }
        }
        // console.log(this.array)
    },
}
</script>

<style lang="scss">
html,body,.shop-box,p,ul,ol,li{
    margin: 0;padding: 0;
    list-style: none;
}
a{text-decoration: none;}
.shop-box{
    width: 100%;height: 100%;
    display: flex;
    flex-direction: column;
}
header{
    width: 100%;height: 1.9rem;
    overflow: hidden;
    position: fixed;
    top: 0;
    .header-one{
        width: 100%;height:6.18rem;
         background: #fff;
        opacity: 0.5;
        img{height: 100%;width: 100%;}
    }
    >.shop-header-ul{
        padding: 0.145rem;
        padding-right:0; 
        height: 1.6rem;width: 100%;
        position: absolute;top: 0;
        >li{
            color: #fff;
             display: flex;
            flex-direction: column;
            justify-content: space-between;
            .header-section{
                display: flex;
                >.div-img{
                img{
                    height: 1.12rem;width: 1.12rem;
                    } 
                }
                div:nth-of-type(2){
                    height: 1.15rem;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    box-sizing: border-box;
                    padding-left:0.145rem; 
                    font-size: 0.24rem;
                    p:nth-of-type(1){
                        font-size: 0.26rem;
                        font-weight: bold;
                    }
                }
            }
            figcaption{
                font-size: 0.2rem;
                span{
                    background: red;
                    padding: 0.015rem 0.04rem;
                    border-radius: 10%;
                    }
            }
        }
    }
}
nav{
    width: 100%;height: 0.5rem;
    background: #fff;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-top:0.13rem;
    padding-bottom:  0.13rem;; 
    font-size: 0.26rem;
    box-shadow:0ch 0.012rem 0  #dddddd;
    position: fixed;
    top: 1.89rem;
    z-index: 2;
}
.shop-con{
    margin-top:2.65rem; 
    height: 100%;
    display: flex;
    flex-direction: column;
    overflow: auto;
    .evaluate{
        width: 100%;height: 10rem;
        img{width: 100%;}
    }
}
.shop-show{
    height: auto;width:100%;
    padding-bottom:0.77rem; 
    z-index: 5;
    >.shop-didi{
        >div{
            width: 100%;height: 0.74rem;
            box-sizing: border-box;
            align-items: center;
            background-color: #eceff1;
            padding: 0.11rem 0.23rem;
            display: flex;
            font-size: 0.3rem;color: #666;
            justify-content: space-between;
            p:nth-of-type(2){
                font-size: 0.26rem;
                display: flex;align-items: center;
            }
        }
        >.shop-show-ul{
            width: 100%;height:0.8rem;
            display: flex;
            justify-content: space-between;
            background-color: #fff ;
            >li{ 
                box-sizing: border-box;
                padding-top: 0.19rem;
                height: 0.8rem;
                border-bottom: 0.05rem solid #fff;
            }
            >li:nth-of-type(1){
                width: 55%;    color: #666;
                font-weight: bold;
                font-size:0.3rem;
                padding-left: 0.23rem;
            }
            >li:nth-of-type(2){
                color: #f60;
                span{
                    font-weight: bold;font-size:0.3rem;
                }
            }
            >li:nth-of-type(3){
                width: 1.08rem;
                display: flex;padding-top: 0.26rem;
                justify-content: space-between;
                button{
                    width: 0.3rem;height: 0.3rem;
                    border-radius: 50%;
                    color: #fff;border: none;
                    background: #3190e8;
                }
            }
        }
    }
    
}
.shop-commodity{
    display: flex;
    .shop-commodity-ul{
        width: 23.5%;height: 34rem;
        overflow: auto;
        >li{
            box-sizing: border-box;
            overflow: hidden;
            width: 100%;
            padding: 0.27rem 0.115rem;
            background-color: #f5f5f5;
            text-align: center;z-index: 1;
            border-bottom: .025rem solid #ededed;
            border-bottom: .025rem solid #ededed;
            >a{ color: #666;font-size: 0.26rem;}
        }
    }
    .shop-commodity-div{
        overflow: auto;
        flex: 1;
        article{
            background: #ededed;
            .shop-hotSale{
                height:0.82rem;
                box-sizing: border-box;
                padding: 0.144rem;
                span:nth-of-type(1){
                    font-size: 0.3rem;
                    font-weight: bold;
                        color: #666;
                }
                span:nth-of-type(2){
                    font-size: 0.2rem;
                    color: #999;
                    padding: 0.1rem;
                }
            }
            >.shop-hotSale-ul{
                width: 100%;height: 2.3rem;
                box-sizing: border-box;
                padding: 0.23rem 0.154rem;
                background: #fff;
                border-bottom: 1px solid #f8f8f8;
                >li{
                    width: 100%;
                    display: flex;
                    img{width: 0.77rem;height: 0.77rem;}
                    >ol{
                        width: 100%;position: relative;
                         box-sizing: border-box;
                         padding-left: 0.154rem;
                        display: flex;
                        flex-direction: column;
                        // justify-content: space-between;
                        >li{
                            box-sizing: border-box;
                            width:100%; padding-top: 0.03rem;
                            font-size: 0.2rem;
                        }
                        >li:nth-of-type(1){
                            font-size: 0.3rem;
                            font-weight: bold;
                        }
                        >li:nth-of-type(2){
                            color: #999;
                        }
                        >li:nth-of-type(4){
                            padding-left: -0.2rem;
                            span{
                                color: rgb(241, 136, 79);
                                border-color: rgb(240, 115, 115); 
                                border: 1px solid currentColor;
                                border-radius: .3rem;
                                padding: .06rem 0.08rem;
                                display: inline-block;
                                transform: scale(.8);
                                // margin-left: 0.35rem;
                            }
                           
                        }
                        >.shop-lastLi{
                            width: 100%;height: 0.34rem;
                            display: flex;align-items: center;
                            justify-content: space-between;
                            position: absolute;bottom: -0.4rem;
                            font-size: 0.3rem;
                            >p:nth-of-type(1){
                                 >span:nth-of-type(1),span:nth-of-type(2){  color:#f60; }
                                 .shop-hotSale-span{font-size: 0.2rem;}    
                            }
                            >p:nth-of-type(2){
                                display: flex;
                                padding-top:0.06rem; 
                                >button{
                                    width: 0.3rem;height: 0.3rem;
                                    border-radius: 50%;
                                    box-sizing: border-box;
                                    line-height: 0.22rem;
                                    padding-bottom:0.07rem;
                                    display: flex;justify-content: center; 
                                }
                                >button:nth-of-type(2){
                                    color:#fff;
                                    background: #3190e8;
                                    border: none;
                                    margin-right:0.154rem;
                                }
                                >button:nth-of-type(1){
                                    color:#3190e8;
                                    background: #fff;
                                    border: 0.01rem solid #3190e8;
                                }
                                >span{font-size: 0.2rem;padding: 0 0.1rem;}
                            }
                           
                        }
                        
                    }
                }   
            }
        }
       
    }
}
.active{
    background-color:#ffffff !important;
    border-left: 0.055rem solid #3190e8;
}

.shop-fot{
    width: 100%;height: 0.77rem;
    z-index: 6;
    // position: relative;
    background: #333;
    color: #fff;
    position: fixed;bottom: 0;
    >div:nth-of-type(1){
        box-sizing: border-box;
        width: 0.9rem;height: 0.9rem;
        padding: 0.154rem;
        background-color: #3d3d3f;
        border: 0.1rem solid #444;
        border-radius: 50%;
        text-align: center;line-height: 0.39rem;
        font-size: 0.46rem;
        position: absolute;
        left: .3rem;
        top: -.3rem;
    }
    >div:nth-of-type(2){
        width: 0.94rem;height: 0.64rem;
        position: absolute;
        left: 22%;top:5%;
        .shop-fot-oneP{ 
            font-size:0.28rem;font-weight: bold;
            line-height: 0.4rem;
        }
        .shop-fot-towP{
            font-size:0.17rem;line-height: 0.2rem;
            box-sizing: border-box;
            padding-left:0.07rem; 
        }
    }
    >div:nth-of-type(3){
        width: 31.2%;height: 100%;
        background-color: #535356;
        float: right; font-size:0.28rem;
        text-align: center;
        line-height: 0.77rem;
        font-weight: bold;
    }
}
.ball{
    width: 0.3rem;height: 0.3rem;
        font-size: 0.16rem;color: #fff;
        text-align: center;
        line-height: 0.3rem;
        background-color: #e42727;
        border-radius: 50%;
        position: absolute;
        left: 1rem;
        top: -.35rem;
}
</style>


